import React, { memo, useEffect, useState } from 'react';
import { connect, useDispatch, useSelector } from 'react-redux';
import { isMobile } from 'react-device-detect';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import { Button, ModalR } from '../common';
import cls from './CustomSportsClub.module.scss';
import { updateComponent, updateComponentItem } from '../../services/components.service';
import ModuleLists from '../SideMenuEntries/ModulesComp/ModuleLists';
import { getFilteredUsers, getUsersByPositionsV2 } from '../../services/user.service';
import { first } from 'rxjs';
import { add } from '../../assets/images/icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faRectangleXmark } from '@fortawesome/free-solid-svg-icons';
import UserCard from '../common/UserCard/UserCard';
import moment, { now } from 'moment';
import { store } from '../../reduxStore/store';
import { setCompanyProcesses, setProcessModule, setUsersArray } from '../../reduxStore/actions/flowActions';
import { APPEND_TO_DATA, generateNewUUID } from '../../shared/utility';
import { processTaskAction, useProcessTaskContext } from '../../context/processTask.context';
import { getProcessById } from '../../services/processes.service';

import * as R from 'ramda';
import { Scheduler } from "@aldabil/react-scheduler";
import { navigateToTransaction } from '../SideMenuEntries/DashboardMobile';
import SelectAndMultiselect from '../SideMenuEntries/ProcessesComps/SelectAndMultiselect/SelectAndMultiselect';
import { purchase } from '../../services/payments.service';

const CustomSportsClub = (props) => {

    const { begunModule, allModules, companyData, companyUsersArray, goto, components, authenticatedUser, user, companyProcesses, template } = props;
    const { title, selectedComponent, setSelectedComponent, step } = props;
    const [loading, setLoading] = useState(false);

    const [isMember, setIsMember] = useState(selectedComponent.shared_users?.filter(usr => usr?.id === authenticatedUser?.id)?.length > 0);
    const [hasMemberRequest, setHasMemberRequest] = useState(selectedComponent.component_items?.filter(it => it?.value === authenticatedUser?.id && it?.type === 'Join' && it?.item_code === begunModule?.processModule?.module?.id)?.length > 0);
    const [showAddModals, setShowAddModals] = useState(false);
    const [showInviteModals, setShowInviteModals] = useState(false);

    const [start, setStart] = useState(false);
    const [refreshComponent, setRefreshComponent] = useState(false);
    const [processTaskContext, setTaskProcessContext] = useProcessTaskContext();

    const isClubAdmin = authenticatedUser?.id === selectedComponent?.created_by?.id;
    const isPrivate = selectedComponent.component_fields.find(cf => cf.field_name === 'Private') ?? false;

    const community = selectedComponent.component_fields.find(cf => cf.field_name === 'My Community')?.value ?? 'Metro Manila';
    const description = selectedComponent.component_fields.find(cf => cf.field_name === 'Club Description')?.value;
    const sport = selectedComponent.component_fields.find(cf => cf.field_name === 'Basketball')?.value === 'Yes' ? 'Basketball' :
        selectedComponent.component_fields.find(cf => cf.field_name === 'Pickleball')?.value === 'Yes' ? 'Pickleball' : 'All Sports';

    const meetsModule = allModules?.find(mod => mod?.name === 'Meets');

    const addItemButton = () => (
        <Button
            light
            style={{ display: isClubAdmin ? '' : 'none' }}
            clicked={() => {
                setShowAddModals(true);
            }}
            icon={<img src={add} />}
        />
    );

    return <div>
        <div style={{ fontFamily: 'var(--app-text-mobile-font)', fontSize: '18px', fontWeight: 'bold' }}>
            {title}
        </div>

        <div style={{ fontFamily: 'var(--app-text-mobile-font)', fontSize: '12px', marginTop: '5px' }}>
            {`${isPrivate ? 'Private' : 'Public'}  ${sport}  ${community}`}
        </div>

        {description &&
            <div style={{ fontFamily: 'var(--app-text-mobile-font)', fontSize: '12px', margin: '40px 0' }}>
                {`${description}`}
            </div>
        }
        {isMember && <p style={{ fontFamily: 'var(--app-text-mobile-font)', fontSize: '14px', fontWeight: 'bold', marginTop: '30px' }}>
            {`You are a member of this club`}
        </p>}
        {isClubAdmin && <p style={{ fontFamily: 'var(--app-text-mobile-font)', fontSize: '14px', fontWeight: 'bold', marginTop: '30px' }}>
            {`You are the admin of this club`}
        </p>}
        <Tabs
            style={{ width: '100%', fontSize: isMobile ? '16px' : '20px', marginTop: '20px' }}
        >
            <TabList style={{ fontFamily: 'var(--app-text-mobile-font)', fontSize: isMobile ? '14px' : '16px' }}>
                {isClubAdmin &&
                    <Tab>
                        {'DETAILS'}
                    </Tab>
                }
                <Tab>
                    {'MEMBERS'}
                </Tab>
                <Tab>
                    {'MEETS'}
                </Tab>

            </TabList>
            {isClubAdmin &&
                <TabPanel id="DETAILS">
                    <div style={{ width: '100%', minHeight: '50vh', overflow: 'auto' }}>
                        {props.children}
                    </div>

                </TabPanel>
            }
            <TabPanel id="MEMBERS">
                <div style={{ width: '100%', minHeight: '100vh', overflow: 'auto' }}>
                    <div className={cls.centeredDiv} style={{ width: 'inherit', fontSize: '14px', marginTop: '20px' }}>
                        <div style={{ fontFamily: 'var(--app-text-main-font)', fontWeight: 'bold', color: 'var(--app-color-gray-dark)', paddingLeft: '5px' }}>{'Club Admin'}</div>
                        <div className={cls.filtersContainer}>
                            <UserCard
                                simple={true}
                                photo={authenticatedUser?.photo ?? 'https://s3-symbol-logo.tradingview.com/logo-yazilim--600.png'}
                                email={`Founded club: ${moment(new Date(selectedComponent.created_date)).format('DD MMM YYYY')}`}
                                position={authenticatedUser?.options?.info ?? ''}
                                firstName={selectedComponent.created_by?.first_name}
                                lastName={selectedComponent.created_by?.last_name}
                                cardClicked={() => { }}
                                width={'100%'}
                            />
                        </div>
                    </div>
                    {selectedComponent?.shared_users?.length > 0 && (<>
                        <p style={{ fontSize: '14px', fontFamily: 'var(--app-text-main-font)', fontWeight: 'bold', color: 'var(--app-color-gray-dark)', paddingLeft: '5px' }}>{`Members ( ${selectedComponent?.shared_users?.length} )`}</p>
                        <ModuleLists
                            className={cls.sharesTable}
                            withColumns={false}
                            modern={true}
                            columns={['User', 'Photo', 'Bio']}
                            list={selectedComponent?.shared_users?.map(val => {
                                const u = companyUsersArray.find(usr => usr.id === val.id);
                                if (u)
                                    return { ['User']: `${u?.first_name} ${u?.last_name}`, ['Photo']: u?.photo ?? companyData?.logo, ['Bio']: u?.options?.info ?? '' };
                                return { User: val?.id };
                            })}
                        />

                    </>)}

                    {isClubAdmin && selectedComponent?.component_items?.filter(it => it.type === 'Join' || it.type === 'Invite').length > 0 && <>
                        <p style={{ fontSize: '14px', fontFamily: 'var(--app-text-main-font)', fontWeight: 'bold', color: 'var(--app-color-gray-dark)', paddingLeft: '5px' }}>{`Invited ( ${selectedComponent?.component_items?.filter(it => it.type === 'Join' || it.type === 'Invite').length} )`}</p>

                        <ModuleLists
                            className={cls.sharesTable}
                            withColumns={false}
                            modern={true}
                            columns={['User', 'Photo', 'Bio']}
                            list={selectedComponent?.component_items?.filter(it => it.type === 'Join' || it.type === 'Invite')?.map(val => {
                                const u = companyUsersArray.find(usr => usr.id === val.value);
                                return { ['User']: `${u?.first_name} ${u?.last_name}`, ['Photo']: u?.photo ?? companyData?.logo, ['Bio']: u?.options?.info ?? '' };
                            })}
                        />

                    </>}
                    {!(isMember || hasMemberRequest || isClubAdmin) && isPrivate &&
                        <Button title={'Request to Join Club'} clicked={() => {
                            setLoading(true);
                            updateComponentItem({ index: selectedComponent?.component_items?.length, item_code: begunModule?.processModule?.module?.id, type: 'Join', value: authenticatedUser?.id }, selectedComponent.id).subscribe({
                                next: () => {
                                    setHasMemberRequest(true);
                                    setLoading(false)
                                },
                                error: () => setLoading(false),
                            });

                        }} style={{ width: '100%' }}
                            loading={loading} />}

                    {!(isMember || isClubAdmin) && !isPrivate &&
                        <Button title={'Join Club'} clicked={() => {
                            setLoading(true);
                            updateComponent(
                                {
                                    ...selectedComponent,
                                    component_items: selectedComponent.component_items.filter(ci => (ci.type === 'Invite' || ci.type === 'Join') && ci.value !== authenticatedUser.id)
                                },
                                selectedComponent.id,
                                [...selectedComponent.shared_users?.map(su => su.id) ?? [], authenticatedUser?.id],
                                title,
                                selectedComponent?.is_complete,
                                begunModule.processModule?.module?.options?.shares, // not component based but module based
                            ).subscribe({
                                next: data => {
                                    selectedComponent.shared_users = data?.data?.shared_users;
                                    setIsMember(true);
                                    setLoading(false);
                                },
                                error: () => setLoading(false),
                            });
                        }} style={{ width: '100%' }}
                            loading={loading} />
                    }


                    {isClubAdmin &&
                        <Button title={'Invite Players'} clicked={() => {
                            setShowInviteModals(true);
                        }} style={{ width: '100%', backgroundColor: 'green' }}
                            loading={loading} />
                    }

                </div>
                {(isMember && !isClubAdmin) &&
                    <Button title={'Leave Club'} clicked={() => {
                        setLoading(true);
                        updateComponent(
                            selectedComponent,
                            selectedComponent.id,
                            selectedComponent.shared_users.filter(su => su.id !== authenticatedUser?.id).map(su => su.id),
                            title,
                            selectedComponent?.is_complete,
                            begunModule.processModule?.module?.options?.shares, // not component based but module based
                        ).subscribe({
                            next: data => {
                                selectedComponent.shared_users = data?.data?.shared_users;
                                setSelectedComponent(selectedComponent);
                                setIsMember(false);
                                setLoading(false);
                            },
                            error: () => setLoading(false),
                        });
                    }} style={{ width: '100%', backgroundColor: 'red' }}
                        loading={loading} />
                }
            </TabPanel>

            <TabPanel id="MEETS">
                <div style={{ width: '100%', minHeight: '60vh', overflow: 'auto', zIndex: '-1' }}>
                    <div style={{ width: '100%', display: 'flex', justifyContent: 'flex-end' }}>
                        {addItemButton()}
                    </div>
                    {!showAddModals &&
                        <ClubsCalendar
                            events={components.filter(co => co.status !== 'Expired' && co.module_id === meetsModule?.id)}
                            isAdmin={isClubAdmin}
                            onEventClick={(event) => {
                                const component = components.find(co => co.id === event?.event_id);
                                if (goto)
                                    navigateToTransaction(companyProcesses, setSelectedComponent, setStart, companyData, component, setTaskProcessContext, authenticatedUser, begunModule, user, allModules, setRefreshComponent, companyUsersArray, meetsModule);

                            }}
                        />
                    }


                </div>
            </TabPanel>

        </Tabs>

        <div className={showInviteModals ? cls.mobilepopupopen : cls.mobilepopup} style={{ display: showInviteModals ? '' : 'none' }}>
            <div style={{ width: '100%', height: '100vh', backgroundColor: 'white', padding: '20px' }} onClick={() => setShowInviteModals(false)} >

                <p className={cls.centerDiv}>
                    {'Invite'}
                </p>

                <ModuleLists
                    className={cls.sharesTable}
                    withColumns={false}
                    modern={true}
                    columns={['User', 'Photo', 'Bio']}
                    list={companyUsersArray.filter(cu => !(selectedComponent?.shared_users?.map(su => su.id)?.includes(cu.id) || selectedComponent?.component_items.filter(ci => ci.type === 'Invite').map(su => su.value)?.includes(cu.id))).map(usr => {
                        return { ['User']: `${usr?.first_name} ${usr?.last_name}`, ['Photo']: usr?.photo ?? companyData?.logo, ['Bio']: usr?.options?.info ?? '', id: usr.id };
                    })}
                    onListItemClick={selected => {

                        setLoading(true);
                        updateComponentItem({ index: selectedComponent?.component_items?.length, item_code: begunModule?.processModule?.module?.id, type: 'Invite', value: selected?.id }, selectedComponent.id).subscribe({
                            next: () => {

                                setLoading(false)
                            },
                            error: () => setLoading(false),
                        });
                    }}
                />


            </div>

        </div>

        <div className={showAddModals ? cls.mobilepopupopen : cls.mobilepopup} style={{ display: showAddModals ? '' : 'none' }}>
            <div style={{ width: '100%', height: '50vh', backgroundColor: 'white', opacity: '0.9' }} onClick={() => setShowAddModals(false)} />
            <div className={cls.mobilePopupActionContainer}>
                <Button title={'Create one-off meet'} style={{ width: '100%' }} clicked={() => {

                    const processData = (data) => {

                        const steps_for_use = data.steps
                            .filter(step => step?.options?.showStepAll == undefined || step?.options?.showStepAll == true || (step?.options?.showStepAll == false && step?.options?.showOnly?.filter(a => authenticatedUser.positions.map(pos => pos.id).indexOf(a) > -1).length > 0 || (step?.options?.showStepAuthor && authenticatedUser?.id === data?.user_id)))
                            .map((step, i) => {
                                return { ...step, rank: i + 1 };
                            });

                        store.dispatch(setProcessModule(data));
                        store.dispatch(setCompanyProcesses([data], APPEND_TO_DATA));
                        store.dispatch(
                            setProcessModule({
                                ...data,
                                completeSteps: data.steps,
                                steps: steps_for_use,
                                stepIndex: 2,
                                module_id: meetsModule.id,
                                module: meetsModule,
                                isUpdating: false,
                            })
                        );

                        setTaskProcessContext({ action: processTaskAction.setCurrentStep, value: data?.steps[2] });

                        setTaskProcessContext({
                            action: processTaskAction.setModuleItems,
                            value: selectedComponent.component_items,
                        });

                        setTaskProcessContext({
                            action: processTaskAction.setModuleNewStepComponents,
                            value: [],
                        });
                        setTaskProcessContext({
                            action: processTaskAction.setComponents,
                            value: [],
                        });

                        const list = [...(steps_for_use[2]?.component_blueprints ?? [])];

                        if (list?.length > 0) {
                            let selectedElement = list.find(li => li.selected_fields.find(sf => sf.field_name === 'Club'))?.selected_fields.find(sf => sf.field_name === 'Club');
                            if (selectedElement) {
                                selectedElement.value = title;
                            }
                            selectedElement = list.find(li => li.selected_fields.find(sf => sf.field_name === 'Sport'))?.selected_fields.find(sf => sf.field_name === 'Sport');
                            if (selectedElement) {
                                selectedElement.value = sport;
                            }
                            setTaskProcessContext({ action: processTaskAction.setComponentBlueprints, value: list });
                            setTaskProcessContext({ action: processTaskAction.setModuleNewStepComponents, value: list });
                        }
                        //clear all values
                        begunModule?.processModule?.steps?.length > 0 && begunModule?.processModule?.steps?.filter(step => step?.component_blueprints?.filter(cb => cb.selected_fields.filter(field => (field.value = undefined))));


                    }

                    if (companyProcesses.find(proc => proc.id === meetsModule.procedure.id)) {
                        processData(companyProcesses.find(proc => proc.id === meetsModule.procedure.id));
                    } else {
                        setLoading(true);
                        getProcessById(meetsModule.procedure.id)
                            .pipe(first())
                            .subscribe({
                                next: data => {
                                    setLoading(false);
                                    processData(data);
                                },
                                error: _error => { },
                            });
                    }
                }} />
                <Button title={'Create recurring schedule'} clicked={() => setShowAddModals(false)} style={{ width: '100%' }} />
            </div>
        </div>


    </div>;
}

const mapStateToProps = store => {
    const {
        authenticatedUser: { position },
        authenticatedUser,
    } = store.authReducer;
    const { user } = store.sessionReducer;

    const { begunModule, allModules, companyData, companyUsersArray, companyPositionsArray, components, companyProcesses } = store.flowReducer;
    return {
        begunModule, allModules, companyData, companyUsersArray, companyPositionsArray, components, authenticatedUser, user, companyProcesses
    };
};


export default connect(mapStateToProps)(CustomSportsClub);

export const CustomSportsMeet = (props) => {
    const { selectedComponent, begunModule, goto } = props;
    const { companyData, companyUsersArray, components } = useSelector(store => store.flowReducer);
    const { authenticatedUser } = useSelector(store => store.authReducer);

    const [loading, setLoading] = useState(false);
    const [hasJoined, setHasJoined] = useState(selectedComponent?.shared_users?.filter(usr => usr?.id === authenticatedUser?.id)?.length > 0);
    const [hasMeetRequest, setHasMeetRequest] = useState(selectedComponent?.component_items?.filter(it => it?.value === authenticatedUser?.id && (it?.type === 'Request' || it?.type === 'Waitlist') && it?.item_code === begunModule?.processModule?.module?.id)?.length > 0);

    const [status, setStatus] = useState(selectedComponent?.component_fields.find(cf => cf.field_name === 'Status'));
    const [payment, setPayment] = useState(selectedComponent?.component_items?.find(it => it.type === 'Payments' && it?.value === authenticatedUser?.id));
    const [message, setMessage] = useState('');
    const [showAddGameModals, setShowAddGameModals] = useState(false);

    const isClubAdmin = authenticatedUser?.id === selectedComponent?.created_by?.id;
    const sport = selectedComponent?.component_fields.find(cf => cf.field_name === 'Sport')?.value;
    const type = selectedComponent?.component_fields.find(cf => cf.field_name === 'Type')?.value;
    const notes = selectedComponent?.component_fields.find(cf => cf.field_name === 'Notes')?.value;
    const clubF = selectedComponent?.component_fields.find(cf => cf.field_name === 'Club');
    const club = components.find(co => co.module_id === clubF?.options?.module_id && co?.component_fields.find(cf => cf.value === clubF?.value))

    const logo = club?.component_fields.find(cf => cf.data_type_name === 'Value Button' && cf?.value);
    const community = club?.component_fields.find(cf => cf.field_name === 'My Community')?.value ?? 'Metro Manila';
    const isPrivate = selectedComponent?.component_fields.find(cf => cf.field_name === 'Private') ?? false;
    const meetDate = `${moment(new Date(selectedComponent?.component_fields.find(cf => cf.field_name === 'Meet Date')?.value)).format('DD MMM YYYY hh:mm A')}`;

    const withFee = selectedComponent?.component_fields.find(cf => cf.field_name === 'Fee');

    const JoinMeet = () => {
        return <div>
            {!(hasJoined || isClubAdmin) && !isPrivate && !(selectedComponent?.status === 'Closed' || selectedComponent?.status === 'Done' || selectedComponent?.status === 'Cancelled' || selectedComponent?.status === 'Expired') &&
                <Button title={'Join Meet'} clicked={() => {
                    setLoading(true);
                    updateComponent(
                        {
                            ...selectedComponent,
                            component_items: selectedComponent.component_items.filter(it => it.value !== authenticatedUser.id && it.type === 'Unavailable')
                        },
                        selectedComponent.id,
                        [...selectedComponent.shared_users?.map(su => su.id) ?? [], authenticatedUser?.id],
                        selectedComponent?.title,
                        selectedComponent?.is_complete,
                        begunModule.processModule?.module?.options?.shares, // not component based but module based
                    ).subscribe({
                        next: data => {
                            selectedComponent.shared_users = data?.data?.shared_users;
                            setHasJoined(true);
                            setLoading(false);
                        },
                        error: () => setLoading(false),
                    });
                }} style={{ width: '100%', backgroundColor: 'green' }}
                    loading={loading}
                />
            }
            {hasJoined && withFee && !isClubAdmin && !payment &&
                <Button
                    disabled={+authenticatedUser?.wallet < +withFee?.value}
                    title={`Pay ${withFee?.field_name}`} clicked={() => {

                        if (+authenticatedUser?.wallet > +withFee?.value) {


                            setLoading(true);

                            purchase({
                                user_id: authenticatedUser.id,
                                id: generateNewUUID(),
                                total: -(+withFee?.value),
                                product: `PY-${selectedComponent?.id}`,
                                notes: `Wallet`,
                                quantity: 1,
                            })
                                .pipe(first())
                                .subscribe({
                                    next: () => {
                                        authenticatedUser.wallet = +(+(authenticatedUser?.wallet ?? 0) - (+withFee?.value));
                                        updateComponentItem({ index: selectedComponent?.component_items?.length, item_code: begunModule?.processModule?.module?.id, type: 'Payments', value: authenticatedUser?.id, alt_value: selectedComponent?.created_by?.id, total: withFee?.value }, selectedComponent.id).subscribe({
                                            next: data => {
                                                setPayment(data?.component_items?.find(it => it.type === 'Payments' && it?.value === authenticatedUser?.id))
                                                purchase({
                                                    user_id: selectedComponent?.created_by?.id,
                                                    id: generateNewUUID(),
                                                    total: (+withFee?.value),
                                                    product: `CR-${selectedComponent?.id}`,
                                                    notes: `Wallet`,
                                                    quantity: 1,
                                                }).pipe(first())
                                                    .subscribe({
                                                        next: () => setLoading(false),
                                                        error: () => setLoading(false),
                                                    });
                                            },
                                            error: () => setLoading(false),
                                        });
                                    },
                                });

                        } else {
                            setMessage(`Insufficient wallet balance. ( Available: ${authenticatedUser?.wallet})`)
                        }


                    }} style={{ width: '100%', backgroundColor: 'green' }}
                    loading={loading}
                />}
            {message && <p style={{ fontFamily: 'var(--app-text-mobile-font)', fontSize: '14px', fontWeight: 'bold' }} onClick={() => setMessage('')}>
                {message}
            </p>}
            {hasJoined && !isClubAdmin && !(selectedComponent?.status === 'Closed' || selectedComponent?.status === 'Done' || selectedComponent?.status === 'Cancelled' || selectedComponent?.status === 'Expired') &&
                <Button title={'Cant Join'} clicked={() => {
                    setLoading(true);
                    updateComponent(
                        {
                            ...selectedComponent,
                            component_items: [...selectedComponent?.component_items ?? [], { index: selectedComponent?.component_items?.length, item_code: begunModule?.processModule?.module?.id, type: 'Unavailable', value: authenticatedUser?.id }]
                        },
                        selectedComponent.id,
                        selectedComponent.shared_users?.filter(su => su.id !== authenticatedUser?.id)?.map(su => su.id),
                        selectedComponent?.title,
                        selectedComponent?.is_complete,
                        begunModule.processModule?.module?.options?.shares, // not component based but module based
                    ).subscribe({
                        next: data => {
                            selectedComponent.shared_users = data?.data?.shared_users;
                            selectedComponent.component_items = data?.data?.component_items;
                            setHasJoined(false);
                            setLoading(false);
                        },
                        error: () => setLoading(false),
                    });
                }} style={{ width: '100%' }}
                    loading={loading}
                />}

            {!(hasJoined || isClubAdmin) && !hasMeetRequest && isPrivate && !(selectedComponent?.status === 'Closed' || selectedComponent?.status === 'Done' || selectedComponent?.status === 'Cancelled' || selectedComponent?.status === 'Expired') &&
                <Button title={'Request to Join Meet'} clicked={() => {
                    setLoading(true);
                    updateComponentItem({ index: selectedComponent?.component_items?.length, item_code: begunModule?.processModule?.module?.id, type: 'Request', value: authenticatedUser?.id }, selectedComponent.id).subscribe({
                        next: () => {
                            setHasMeetRequest(true);
                            setLoading(false)
                        },
                        error: () => setLoading(false),
                    });

                }} style={{ width: '100%', backgroundColor: 'var(--app-color-blue)' }}
                    loading={loading} />}


        </div>
    }

    const addItemButton = () => (
        <Button
            light
            style={{ display: isClubAdmin ? '' : '' }}
            clicked={() => {
                setShowAddGameModals(true);
            }}
            icon={<img src={add} />}
        />
    );

    return <div>
        <div style={{ fontFamily: 'var(--app-text-mobile-font)', fontSize: '18px', fontWeight: 'bold' }}>
            {selectedComponent?.title}
        </div>
        <div style={{ fontFamily: 'var(--app-text-mobile-font)', fontSize: '12px', marginTop: '5px' }}>
            {meetDate}
        </div>

        <div style={{ display: 'flex', justifyContent: 'space-between' }}>
            {hasJoined && <p style={{ fontFamily: 'var(--app-text-mobile-font)', fontSize: '14px', fontWeight: 'bold', marginTop: '30px' }}>
                {`You are a participant of this meet`}
            </p>}
            {!isClubAdmin && withFee && <p style={{ fontFamily: 'var(--app-text-mobile-font)', fontSize: '14px', fontWeight: 'bold', marginTop: '30px' }}>
                {payment ? 'PAID' : `FEE (${withFee?.value})`}
            </p>}
        </div>
        <Tabs
            style={{ width: '100%', fontSize: isMobile ? '16px' : '20px', marginTop: '20px' }}
        >
            <TabList style={{ fontFamily: 'var(--app-text-mobile-font)', fontSize: isMobile ? '14px' : '16px' }}>
                <Tab>
                    {'DETAILS'}
                </Tab>
                <Tab>
                    {'PARTICIPANTS'}
                </Tab>
                {!(selectedComponent.status === 'New' || selectedComponent.status === 'None') && <Tab>
                    {'GAMES'}
                </Tab>}

            </TabList>
            <TabPanel id='DETAILS'>
                <div style={{ width: '100%', minHeight: '60vh', overflow: 'auto' }}>
                    <div className={cls.centeredDiv} style={{ width: 'inherit', fontSize: '14px', marginTop: '20px' }}>
                        {props.children}
                        {isClubAdmin && selectedComponent?.status !== 'Expired' && <div style={{ width: '100%', display: '' }}>
                            <SelectAndMultiselect
                                titleProps={{ fontSize: '12px', color: 'var(--app-color-gray-light)' }}
                                className={cls.multiselectComponent}
                                visible={true}
                                mandatory={false}
                                value={status?.value}

                                items={status?.options?.list?.map(li => { return { label: li, value: li } })}
                                onChange={(value) => {
                                    selectedComponent.status = value;
                                    status.value = value;
                                    setLoading(true);
                                    updateComponent(
                                        selectedComponent,
                                        selectedComponent.id,
                                        selectedComponent.shared_users?.map(su => su.id),
                                        selectedComponent?.title,
                                        selectedComponent?.is_complete,
                                        begunModule.processModule?.module?.options?.shares, // not component based but module based
                                    ).subscribe({
                                        next: data => {
                                            setLoading(false);
                                        },
                                        error: () => setLoading(false),
                                    });
                                }}

                                fieldLabel={'Status'}
                                placeholder={'Select status...'}
                            />
                        </div>}
                    </div>

                    <JoinMeet />
                </div>
            </TabPanel>
            <TabPanel id="PARTICIPANTS">
                <div style={{ width: '100%', minHeight: '50vh', overflow: 'auto' }}>
                    <p style={{ fontSize: '14px', fontFamily: 'var(--app-text-main-font)', fontWeight: 'bold', color: 'var(--app-color-gray-dark)', paddingLeft: '5px' }}>{`Organizer`}</p>
                    <div className={cls.filtersContainer}>
                        <UserCard
                            simple={true}
                            photo={authenticatedUser?.photo ?? 'https://s3-symbol-logo.tradingview.com/logo-yazilim--600.png'}
                            email={``}
                            position={authenticatedUser?.options?.info ?? ''}
                            firstName={selectedComponent?.created_by?.first_name}
                            lastName={selectedComponent?.created_by?.last_name}
                            cardClicked={() => { }}
                            width={'100%'}
                        />
                    </div>
                    {selectedComponent?.shared_users?.length > 0 && (<>
                        <p style={{ fontSize: '14px', fontFamily: 'var(--app-text-main-font)', fontWeight: 'bold', color: 'var(--app-color-gray-dark)', paddingLeft: '5px' }}>{`Participants ( ${selectedComponent?.shared_users?.length} )`}</p>

                        <ModuleLists
                            className={cls.sharesTable}
                            withColumns={false}
                            modern={true}
                            columns={['User', 'Photo', 'Bio', 'Pay']}
                            list={selectedComponent?.shared_users?.map(val => {
                                const u = companyUsersArray.find(usr => usr.id === val.id);
                                if (u)
                                    return {
                                        ['User']: `${u?.first_name} ${u?.last_name}`,
                                        ['Photo']: u?.photo ?? companyData?.logo,
                                        ['Bio']: u?.options?.info ?? '',
                                        ['Pay']: (withFee && isClubAdmin) ? selectedComponent?.component_items?.find(it => it.type === 'Payments' && it?.value === u?.id) ? 'Paid' :

                                            <div style={{ display: 'flex', justifyContent: 'space-between' }}>
                                                <p style={{ fontFamily: 'var(--app-text-mobile-font)', fontSize: '14px', fontWeight: 'bold' }}>
                                                    {'Unpaid'}
                                                </p>
                                                <Button title={'Remove'} clicked={() => {
                                                    setLoading(true);
                                                    updateComponent(
                                                        selectedComponent,
                                                        selectedComponent.id,
                                                        selectedComponent.shared_users?.filter(su => su.id !== u?.id).map(su => su.id),
                                                        selectedComponent?.title,
                                                        selectedComponent?.is_complete,
                                                        begunModule.processModule?.module?.options?.shares, // not component based but module based
                                                    ).subscribe({
                                                        next: data => {

                                                            selectedComponent.component_items = data?.data?.component_items;
                                                            setHasJoined(false);
                                                            setLoading(false);
                                                        },
                                                        error: () => setLoading(false),
                                                    });
                                                }} style={{ width: '100px' }}
                                                    loading={loading}
                                                />
                                            </div>

                                            : ''
                                    };
                                return { User: val?.id };
                            })}
                        />
                    </>)}
                    {isClubAdmin && selectedComponent?.component_items?.filter(it => it.type === 'Unavailable').length > 0 && <>
                        <p style={{ fontSize: '14px', fontFamily: 'var(--app-text-main-font)', fontWeight: 'bold', color: 'var(--app-color-gray-dark)', paddingLeft: '5px' }}>{`Unavailable ( ${selectedComponent?.component_items?.filter(it => it.type === 'Unavailable').length} )`}</p>

                        <ModuleLists
                            className={cls.sharesTable}
                            withColumns={false}
                            modern={true}
                            columns={['User', 'Photo', 'Bio']}
                            list={selectedComponent?.component_items?.filter(it => it.type === 'Unavailable')?.map(val => {
                                const u = companyUsersArray.find(usr => usr.id === val.value);
                                return { ['User']: `${u?.first_name} ${u?.last_name}`, ['Photo']: u?.photo ?? companyData?.logo, ['Bio']: u?.options?.info ?? '' };
                            })}
                        />

                    </>}
                    {isClubAdmin && selectedComponent?.component_items?.filter(it => it.type === 'Request' || it.type === 'Waitlist').length > 0 &&
                        <>
                            <p style={{ fontSize: '14px', fontFamily: 'var(--app-text-main-font)', fontWeight: 'bold', color: 'var(--app-color-gray-dark)', paddingLeft: '5px' }}>{`Request to Join ( ${selectedComponent?.component_items?.filter(it => it.type === 'Request' || it.type === 'Waitlist').length} )`}</p>


                            {selectedComponent?.component_items?.filter(it => it.type === 'Request' || it.type === 'Waitlist')?.map((val, i) => {
                                const u = companyUsersArray.find(usr => usr.id === val.value);
                                return <div key={i} style={{ display: 'flex' }} className={cls.filtersContainer}>
                                    <p style={{ fontSize: '14px', fontFamily: 'var(--app-text-main-font)', width: '200px', marginRight: '20px' }}>
                                        {`${u?.first_name} ${u?.last_name}`}
                                    </p>
                                    <Button title={'Approve'} clicked={() => {
                                        setLoading(true);
                                        updateComponent(
                                            {
                                                ...selectedComponent,
                                                component_items: selectedComponent.component_items.filter(it => it.value !== u.id && (it.type === 'Request' || it.type === 'Waitlist'))
                                            },
                                            selectedComponent.id,
                                            [...selectedComponent.shared_users?.map(su => su.id) ?? [], u?.id],
                                            selectedComponent?.title,
                                            selectedComponent?.is_complete,
                                            begunModule.processModule?.module?.options?.shares, // not component based but module based
                                        ).subscribe({
                                            next: data => {
                                                selectedComponent.shared_users = data?.data?.shared_users;
                                                selectedComponent.component_items = data?.data?.component_items;
                                                setLoading(false);
                                            },
                                            error: () => setLoading(false),
                                        });
                                    }} style={{ width: '100px', backgroundColor: 'green' }}
                                        loading={loading}
                                    />
                                    <Button title={'Decline'} clicked={() => {
                                        setLoading(true);
                                        updateComponent(
                                            {
                                                ...selectedComponent,
                                                component_items: selectedComponent.component_items.filter(it => it.value !== u.id && (it.type === 'Request' || it.type === 'Waitlist'))
                                            },
                                            selectedComponent.id,
                                            selectedComponent.shared_users?.map(su => su.id),
                                            selectedComponent?.title,
                                            selectedComponent?.is_complete,
                                            begunModule.processModule?.module?.options?.shares, // not component based but module based
                                        ).subscribe({
                                            next: data => {

                                                selectedComponent.component_items = data?.data?.component_items;
                                                setHasJoined(false);
                                                setLoading(false);
                                            },
                                            error: () => setLoading(false),
                                        });
                                    }} style={{ width: '100px' }}
                                        loading={loading}
                                    />
                                    {val.type === 'Request' ? <Button title={'Waitlist'} clicked={() => {
                                        setLoading(true);


                                        updateComponentItem({ ...val, type: 'Waitlist' }, selectedComponent.id).subscribe({
                                            next: data => {
                                                val.type = 'Waitlist';
                                                setLoading(false)
                                            },
                                            error: () => setLoading(false),
                                        });



                                    }} style={{ width: '100px', backgroundColor: 'orange' }}
                                        loading={loading}
                                    /> : <p style={{ fontSize: '16px', fontFamily: 'var(--app-text-main-font)', fontWeight: 'bold' }}>{'Waitlisted'}</p>}
                                </div>
                            })
                            }



                        </>}

                    {hasMeetRequest && <>
                        <p style={{ fontSize: '14px', fontFamily: 'var(--app-text-main-font)', fontWeight: 'bold', color: 'var(--app-color-gray-dark)', paddingLeft: '5px' }}>{selectedComponent.component_items?.filter(it => it?.value === authenticatedUser?.id && (it?.type === 'Request') && it?.item_code === begunModule?.processModule?.module?.id)?.length > 0 ? `Pending Request` : 'Waitlisted'}</p>
                        <div className={cls.filtersContainer}>
                            <UserCard
                                simple={true}
                                photo={authenticatedUser?.photo ?? 'https://s3-symbol-logo.tradingview.com/logo-yazilim--600.png'}
                                email={``}
                                position={authenticatedUser?.options?.info ?? ''}
                                firstName={authenticatedUser.first_name}
                                lastName={authenticatedUser.last_name}
                                cardClicked={() => { }}
                                width={'100%'}
                            />
                        </div>
                    </>}
                    <JoinMeet />
                </div>
            </TabPanel>
            {!(selectedComponent.status === 'New' || selectedComponent.status === 'None') &&
                <TabPanel id="GAMES">
                    <div style={{ width: '100%', minHeight: '50vh', overflow: 'auto' }}>
                        <div style={{ width: '100%', display: 'flex', justifyContent: 'flex-end' }}>
                            {addItemButton()}
                        </div>
                    </div>

                    <div className={showAddGameModals ? cls.mobilepopupopen : cls.mobilepopup} style={{ display: showAddGameModals ? '' : 'none' }}>
                        <div style={{ width: '100%', height: '50vh', backgroundColor: 'white', opacity: '0.9' }} onClick={() => setShowAddGameModals(false)} />
                        <div className={cls.mobilePopupActionContainer}>
                            <Button title={'Create one-off meet'} style={{ width: '100%' }} clicked={() => {
                            }} />
                           
                        </div>
                    </div>

                </TabPanel>}

        </Tabs>


    </div>;


}

export const CustomSportsClubStep = (props) => {
    const { begunModule, authenticatedUser, user, template } = props;

    const { companyData, companyUsersArray, components, companyPositionsArray, allModules, companyProcesses } = useSelector(store => store.flowReducer);
    const [, setLoading] = useState(false);
    const [, setStart] = useState(false);
    const [, setRefreshComponent] = useState(false);
    const [, setSelectedComponent] = useState(false);
    const [, setTaskProcessContext] = useProcessTaskContext();

    const invites = components.filter(co => co.module_id === begunModule.processModule.module_id && co.component_items.find(ci => (ci.type === 'Invite' || ci.type === 'Join') && ci.value === authenticatedUser.id)).map(co => {
        return { ...co.component_items.find(ci => (ci.type === 'Invite' || ci.type === 'Join') && ci.value === authenticatedUser.id), club: co };
    });
    return <div style={{ width: '100%', padding: '0 20px', fontFamily: 'var(--app-text-main-font)' }}>

        {/* <p style={{ color: 'gray' }}>
            {'Activities'}
        </p> */}
        {invites?.map((invite, index) => {

            return <div className={cls.filtersContainer} key={index}>
                <UserCard
                    simple={true}
                    photo={'https://s3-symbol-logo.tradingview.com/logo-yazilim--600.png'}
                    firstName={invite.club.title}
                    email={``}
                    position={'has invited you to join their club'}
                    cardClicked={() => {
                        setLoading(true);
                        updateComponent(
                            {
                                ...invite.club,
                                component_items: invite.club.component_items.filter(it => it.id !== invite.id)
                            },
                            invite.club.id,
                            [...invite.club.shared_users?.map(su => su.id) ?? [], authenticatedUser?.id],
                            invite.club.title,
                            invite.club?.is_complete,
                            begunModule.processModule?.module?.options?.shares, // not component based but module based
                        ).subscribe({
                            next: data => {
                                invite.club.shared_users = data?.data?.shared_users;
                                // setIsMember(true);
                                setLoading(false);
                            },
                            error: () => setLoading(false),
                        });
                    }}
                    width={'100%'}
                />
            </div>
        })}


    </div>
}



const ClubsCalendar = props => {
    const { events, onEventClick, isAdmin = true } = props;
    const [calendarView, setCalendarView] = useState('day');

    return <Scheduler
        style={{ fontFamily: 'var(--app-text-main-font)', fontSize: '30px' }}

        events={events?.map(event => {
            const start = event.component_fields.find(cf => cf.field_name === 'Meet Date' && cf.data_type_name === 'Date')?.value;
            const ends = event.component_fields.find(cf => cf.field_name === 'Ends' && cf.data_type_name === 'Date')?.value
            return {
                event_id: event.id,
                title: event.title,
                start: new Date(Date.parse(start)),
                end: new Date(Date.parse(ends)),
                admin_id: event.id,
                editable: false,
                deletable: false,
            }
        })}
        agenda={true}
        navigation={isAdmin}
        alwaysShowAgendaDays={false}
        view={calendarView}

        onEventClick={onEventClick}
        onViewChange={view => setCalendarView(view)}
    />
}